import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "../Body.css"; // For smooth CSS transitions
import * as motion from "motion/react-client";

import TypingEffect from 'react-typed.ts';

function Body() {
  const [entries, setEntries] = useState([]);
  const [formData, setFormData] = useState({ email: "", password: "" });
  const [isEditing, setIsEditing] = useState(false);
  const [editIndex, setEditIndex] = useState(null);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (isEditing) {
      const updatedEntries = [...entries];
      updatedEntries[editIndex] = formData;
      setEntries(updatedEntries);
      setIsEditing(false);
      setEditIndex(null);
    } else {
      setEntries([...entries, formData]);
    }
    setFormData({ email: "", password: "" });
  };
  const handleDelete = (index) => {
    // Trigger the exit animation and then remove the entry after a delay
    setTimeout(() => {
      const updatedEntries = entries.filter((_, i) => i !== index);
      setEntries(updatedEntries);
    }, 400); // Adjust delay to match the exit animation duration (in ms)
  };
  

  const handleEdit = (index) => {
    setFormData(entries[index]);
    setIsEditing(true);
    setEditIndex(index);
  };

  return (
    <div className="container mt-5">
      <br></br>
      <h1 className="text-center">
        Testing 
      <TypingEffect
        strings={[
          ' Simple',
          " Cute",
          ' Robust'
        ]}
        typeSpeed={50}
        backSpeed={70}
        startDelay={150}
        loop={true}
      />
      <br>
      </br>
      Application
          
    </h1>
      <form onSubmit={handleSubmit}>
        <div className="mb-3">
          <label htmlFor="email" className="form-label">
            Email Address
          </label>
          <input
            type="Text"
            name="email"
            className="form-control"
            id="email"
            value={formData.email}
            onChange={handleInputChange}
          />
        </div>
        <div className="mb-3">
          <label htmlFor="password" className="form-label">
            Password
          </label>
          <input
            type="Text"
            name="password"
            className="form-control"
            id="password"
            value={formData.password}
            onChange={handleInputChange}
          />
        </div>
        <div className="text-center">
          <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
              onHoverStart={() => console.log("Delete button hover started!")}
              type="submit"
              className="btn btn-sm btn-outline-primary mx-2 rounded-pill"
            >
              {isEditing ? "Update" : "Submit"}
          </motion.button>
        </div>
      </form>
      <br />
      <table className="table table-striped">
        <thead className="text-center">
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Email</th>
            <th scope="col">Password</th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          {entries.map((entry, index) => (
            <motion.tr
              key={index}
              initial={{ opacity: 0, scale: 0 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                duration: 0.4,
                scale: { type: "spring", duration: 0.4, bounce: 0.5 },
              }}
              className="fade-in text-center"
            >
              <td>{index + 1}</td>
              <td >
                {entry.email}
              </td>
              <td>{entry.password}</td>
              <td>
              <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
              onHoverStart={() => console.log("Delete button hover started!")}
              type="button"
              className="btn btn-sm btn-outline-danger mx-2 rounded-pill"
              onClick={() => handleDelete(index)}
            >
              Delete
            </motion.button>
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
              onHoverStart={() => console.log("Update button hover started!")}
              type="button"
              className="btn btn-sm btn-outline-dark rounded-pill"
              onClick={() => handleEdit(index)}
            >
              Update
            </motion.button>
              </td>
            </motion.tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Body;
